html {
  text-align: center;
  background: rgb(13, 41, 63);
}

body {
  color: white;
  margin: 3px !important;
}

.item-image {
  border: 2px solid white;
  margin: 0.2rem;
  width: 72px;
}

.item-image-empty {
  border-color: transparent !important;
}

code {
  display: block;
  white-space: pre-wrap;
  text-align: left;
}

.items-section {
  max-width: 420px; /* 380px for 64px item images */
  margin: 0 auto;
  margin-bottom: 2rem;
  line-height: 0;
}

.items-section .title {
  width: 100%;
  border-bottom: 1px solid white;
  color: white;
  text-transform: uppercase;
  text-align: left;
}

.navigation button {
  padding: 0.7rem;
  margin: 0.3rem;
  background-color: transparent;
  color: white;
  border: 2px solid white;
  text-transform: uppercase;
  font-weight: 600;
  cursor: pointer;
}


